.remove-favorite {
	display: inline-block;
	transition: all .3s;
	color: #999;
	font-size: .9rem;
	cursor: pointer;
	padding: 4px 6px;
	border-radius: 3px;
}

.remove-favorite:hover {
	background-color: #DEDEDE;
}

.page-section {
	padding-top: 1.6rem;
	padding-bottom: 1.6rem;

	@include atSmall {
		padding-top: 3.5rem;
		padding-bottom: 3.5rem;
	}

	&--white {
		background-color: #FFF;
	}

	&--beige {
		background-color: $beige;
	}

	p:last-of-type {
		margin-bottom: 0;
	}
}

.school-logo-text {
	position: relative;
	z-index: 20;
	margin: 0;
	font-size: 1.75rem;
	font-weight: 100;
}

.school-logo-text strong {
	font-weight: 400;
}

.school-logo-text a {
	color: #FFF;
	text-decoration: none;
}

.school-logo-text--alt-color a {
	color: #666;

	&:hover {
		color: $mainBlue;
		text-decoration: underline;
	}
}

@keyframes spin {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}

	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

.spinner-loader {
	margin-top: 45px;
	border-radius: 50%;
	width: 24px;
	height: 24px;
	border: 0.25rem solid rgba(0, 0, 0, 0.2);
	border-top-color: black;
	-webkit-animation: spin 1s infinite linear;
	animation: spin 1s infinite linear;
}

.c-orange {
	color: $darkOrange
}

.c-blue {
	color: $mainBlue
}

.c-white {
	color: #FFF
}

.c-dark {
	color: #333
}

.gray {
	color: #999999
}

.search-trigger {
	display: none;
	color: #FFF;
	position: relative;
	cursor: pointer;
	top: 1px;
	margin-left: 20px;

	@include atMedium {
		display: inline-block;
		top: 6px;
	}
}

.search-trigger:hover {
	opacity: .65;
}

.social-icons-list {
	display: flex;
	margin: 0 -8px;
}

.social-icons-list li {
	flex: 1;
	margin: 0 8px;
}

.social-icons-list li a {
	display: block;
	text-align: center;
	color: #FFF;
	font-size: 1.25rem;
}

.social-icons-list li a:hover {
	opacity: .75;
}

.social-color-facebook {
	background-color: #4862a3
}

.social-color-twitter {
	background-color: #55acee
}

.social-color-youtube {
	background-color: #cc1e1f
}

.social-color-linkedin {
	background-color: #0077b5
}

.social-color-instagram {
	background-color: #d8226b
}

.nav-list ul {
	padding: 0;
	margin: 0;
}

.nav-list li {
	list-style: none;
}

.nav-list a {
	font-size: .9rem;
	color: #999;
	text-decoration: none;
}

.nav-list a:hover {
	color: $mainBlue;
	text-decoration: underline;
}

.one-half,
.one-third,
.one-fourth,
.one-fifth,
.one-sixth {
	padding-bottom: 1.6rem;
}

@include atMedium {
	.row {
		margin-right: -50px;
	}

	.one-half,
	.one-third,
	.two-thirds,
	.one-fourth,
	.one-fifth,
	.one-sixth {
		padding-bottom: 0;
		float: left;
		box-sizing: border-box;
		padding-right: 50px;
	}

	.two-thirds {
		width: 66.66%;
	}

	.one-half {
		width: 50%
	}

	.one-third {
		width: 33.33%
	}

	.one-quarter {
		width: 25%
	}

	.one-fifth {
		width: 20%
	}

	.one-sixth {
		width: 16.66%
	}
}

/* Make Top WP Admin Bar Fixed on Mobile */
@media screen and (max-width: 600px) {
	#wpadminbar {
		position: fixed;
	}
}

/* Seach Form */
.search-form {
	background-color: #DEDEDE;
	padding: 20px;
	text-align: center;
}

.search-form label {
	display: block;
}

.search-form-row {
	display: flex;
}

.search-form .s {
	min-width: 20px;
	flex-basis: auto;
	flex-grow: 1;
	border: none;
	font-family: 'Roboto', sans-serif;
	padding: 10px;

	@include atSmall {
		font-size: 1.4rem;
	}
}

.search-submit {
	color: #FFF;
	border: none;
	border-radius: 4px;
	outline: none;
	background-color: $mainBlue;
	font-family: 'Roboto', sans-serif;
	padding: 0 20px;
	margin-left: 10px;
}

.note-limit-message {
	visibility: hidden;
	opacity: 0;
	transition: all .3s ease-out;
	color: #c32929;
	font-weight: bold;
}

.note-limit-message.active {
	visibility: visible;
	opacity: 1;
}

/* Like Box */
.generic-content .like-box {
	float: right;
	font-size: 1rem;
	background-color: #EDEDED;
	box-shadow: 1px 1px 1px rgba(0, 0, 0, .25);
	color: red;
	padding: 10px 10px 10px 25px;
	border-radius: 4px;
	cursor: pointer;
	margin: 0 0 30px 30px;
	position: relative;
}

.generic-content .like-box:hover {
	background-color: #DEDEDE;
}

.like-count {
	padding-left: 10px;
}

.like-box .fa-heart-o {
	transition: all .35s ease-out;
	position: absolute;
	left: 10px;
}

.like-box .fa-heart {
	left: 10px;
	transition: all .4s ease-out;
	position: absolute;
	visibility: hidden;
	transform: scale(.2);
	opacity: 0;
}

.like-box[data-exists="yes"] .fa-heart {
	transform: scale(1);
	visibility: visible;
	opacity: 1;
}

.like-box[data-exists="yes"] .fa-heart-o {
	visibility: hidden;
	opacity: 0;
}